import React, { useEffect, useState } from 'react';
import TestVideo from '../components/TestVideo';
import TestVideoDouble from '../components/TestVideoDouble';

import CompareTests from '../components/CompareTests';
import CompareTestsDouble from '../components/CompareTestsDouble';

import { Button, Row, Col } from 'react-bootstrap'
import LoadingModal from '../components/LoadingModal'


const UserPage = ({ token, email, userPageState, setUserPageState, isGenerating, setIsGenerating }) => {

    // const [userPageState, setUserPageState] = useState("")
    const [userType, setUserType] = useState("")
    const [loadingShow, setLoadingShow] = useState(false);
    
    const [testVideoData, setTestVideoData] = useState({"imageType": "", "imageData": "", "videoData": false, "checked": true, "message": false})
    const [testVideoDoubleData, setTestVideoDoubleData] = useState({"imageUrl1": "", "imageUrl2": "", "videoData": false, "checked": true, "message": false})

    const main = (userPageState) => {
        switch (userPageState) {

            case "compareTests":
                return <CompareTests token={token} setUserPageState={setUserPageState} className='mt-4' />

            case "compareTestsDouble":
                return <CompareTestsDouble token={token} setUserPageState={setUserPageState} className='mt-4' />

            case "testVideo":
                return <TestVideo token={token} setUserPageState={setUserPageState} className='mt-4' isGenerating={isGenerating} 
                setIsGenerating={setIsGenerating}  testVideoData={testVideoData} setTestVideoData={setTestVideoData}/>
            case "testVideoDouble":
                return <TestVideoDouble token={token} setUserPageState={setUserPageState} className='mt-4' isGenerating={isGenerating} 
                setIsGenerating={setIsGenerating}  testVideo2={testVideoDoubleData} setTestVideo2={setTestVideoDoubleData}/>
            default:
                return <div className='mt-5'>
                    <Row className="justify-content-md-center" md="auto"    >
                        <Col xs lg="">
                            {userType === "admin" &&
                                <>
                                    <Button variant="primary" size="sm" className='ms-3 me-1' onClick={() => { setUserPageState("testVideo"); }}>
                                        Generate Videos (1 image)
                                    </Button>

                                    <Button variant="primary" size="sm" className='me-4 ms-1' onClick={() => { setUserPageState("compareTests"); }}>
                                        Results
                                    </Button>

                                    <Button variant="primary" size="sm" className='ms-4 me-1' onClick={() => { setUserPageState("testVideoDouble"); }}>
                                        Generate Videos (2 images)
                                    </Button>

                                    <Button variant="primary" size="sm" className='me-3 ms-1' onClick={() => { setUserPageState("compareTestsDouble"); }}>
                                        Results
                                    </Button>
                                </>
                            }
                        </Col>
                    </Row>
                </div>
        }
    }
    useEffect(() => {
        async function getUser() {
            setLoadingShow(true)

            let res = await fetch(`https://shoevideo.azurewebsites.net/api/get_user_info`, {
                method: "POST",
                body: JSON.stringify({
                    'email': email,

                }),
                headers: {
                    'Content-Type': 'application/json',
                    'Accept': 'application/json',
                    'x-functions-key': token
                },
            })

            let resData = await res.json();
            if (res.ok) {
                setUserType(resData["user_type"])
                setLoadingShow(false)
            }
            else {
                setLoadingShow(false)
            }
        }
        getUser();

    }, [email, token]);

    return (
        <div>
            <LoadingModal show={loadingShow} text={'Getting user data'} />
            {main(userPageState)}
        </div >
    );
};
export default UserPage;
