import React, { useState } from 'react';
import { Button, Form, Row, Col, Container, Alert, FloatingLabel } from 'react-bootstrap'

const TestVideoDouble = ({ token, setUserPageState, isGenerating, setIsGenerating,
    testVideo2, setTestVideo2 }) => {

    let handleSubmit = async (e) => {
        setIsGenerating(true)
        e.preventDefault();
        var start = window.performance.now();

        setTestVideo2({ ...testVideo2, videoData: false, message: false })


        let res = await fetch(`https://shoevideo.azurewebsites.net/api/test_video_double`, {
            method: "POST",
            body: JSON.stringify({
                'img_url_1': testVideo2.imageUrl1,
                'img_url_2': testVideo2.imageUrl2,
                'is_save': testVideo2.checked

            }),
            headers: {
                'Content-Type': 'application/json',
                'Accept': 'application/json',
                'x-functions-key': token
            },
        })

        let resData = await res.json();
        if (res.ok) {

            var end = window.performance.now();
            setTestVideo2({
                ...testVideo2, videoData: resData['result_url'],
                message: { "status": "success", "message": "Video successfully generated in " + ((end - start) / 1000).toFixed(2) + " seconds." }
            })

            setIsGenerating(false)
        }
        else {


            if (res.status === 404) {
                setTestVideo2({ ...testVideo2, message: { "status": "danger", "message": resData['detail'], "time": "" } })
            }
            else {
                setTestVideo2({ ...testVideo2, message: { "status": "danger", "message": "Error code: " + (res.status).toString(), "time": "" } })
            }
            setIsGenerating(false)
        }
    }

    return (
        <Container style={{}}>
            <Row className="mt-4">

                <Col xs lg="2" >
                    <Button variant="warning" size="sm" className='' onClick={() => { setUserPageState(""); }}>
                        Back
                    </Button>
                </Col>
                <Col xs lg="8">


                    {/* <LoadingModal show={loadingShow} text={'Generating video'} /> */}
                    <Form onSubmit={handleSubmit} className='mb-4'>
                        <Form.Group className="mb-3">
                            <FloatingLabel label="Image 1 URL">
                                <Form.Control type="text" placeholder="Image 1 URL"
                                    value={testVideo2.imageUrl1} onInput={e => setTestVideo2({ ...testVideo2, imageUrl1: e.target.value })} required />
                            </FloatingLabel>

                        </Form.Group>
                        <Form.Group className="mb-3">
                            <FloatingLabel label="Image 2 URL">

                            <Form.Control type="text" placeholder="Image 2 URL"
                                value={testVideo2.imageUrl2} onInput={e => setTestVideo2({ ...testVideo2, imageUrl2: e.target.value })} required />
                            </FloatingLabel>

                        </Form.Group>

                        <Row>
                            <Col xs lg="3">
                                <Button variant="primary" type="submit" disabled={isGenerating} style={{ width: "160px" }}>
                                    {isGenerating ? 'Generating video...' : 'Submit'}
                                </Button>
                            </Col>

                            <Col className='mx-2'>
                                <Form.Group>

                                    <div className="form-check form-switch">
                                        <Form.Control className="form-check-input" type="checkbox" checked={testVideo2.checked}
                                            onChange={(e) => setTestVideo2({ ...testVideo2, checked: e.currentTarget.checked })} />
                                        <label className="form-check-label text-muted">Add to results</label>
                                    </div>
                                </Form.Group>
                            </Col>

                        </Row>

                    </Form>


                    <Row style={{ height: '400px' }}>
                        <Col xs lg="4">
                            <img src={testVideo2.imageUrl1} style={{ width: "90%" }} alt="" />

                        </Col>
                        <Col xs lg="4">
                            <img src={testVideo2.imageUrl2} style={{ width: "90%" }} alt="" />

                        </Col>
                        <Col xs lg="4">
                            {testVideo2.videoData &&
                                <video width="90%" autoPlay loop>
                                    <source src={testVideo2.videoData} type="video/mp4"></source>
                                </video>
                            }
                        </Col>
                    </Row>

                    {testVideo2.message &&

                        <Alert variant={testVideo2.message['status']}>
                            {testVideo2.message['message']}
                        </Alert>
                    }


                </Col>
            </Row>
        </Container>
    );
}

export default TestVideoDouble